import { NgModule, Input } from '@angular/core';
import { CommonModule } from '@angular/common';

import { AuthRoutingModule } from './auth-routing.module';
import { ForgetPasswordComponent, LoginComponent, NewPasswordComponent, RegisterBusinessUserComponent, RegisterEntryPointComponent, RegisterNormalUserComponent } from './components';
import { FormsDependenciesModule } from 'src/app/forms-dependencies/forms-dependencies.module';
import { AuthApiService } from './services/api/auth-api.service';
import { SharedModule } from './../../shared/shared.module';


import { MatFormFieldModule } from '@angular/material/form-field';
import { MatInputModule } from '@angular/material/Input';
import { MatCheckboxModule } from '@angular/material/checkbox';
import { ReactiveFormsModule } from '@angular/forms';
import { MatButtonModule } from '@angular/material/button';
import { MatTabsModule } from '@angular/material/tabs';
import { MatSelectModule } from '@angular/material/select';

import { CompanyInfoComponent } from './components/register/_controls/company-info/company-info.component';
import { StoreInfoComponent } from './components/register/_controls/store-info/store-info.component';
import { ContactInfoComponent } from './components/register/_controls/contact-info/contact-info.component';
import { BasicInfoComponent } from './components/register/_controls/basic-info/basic-info.component';
import { ConfirmEmailComponent } from './components/confirm-email/confirm-email.component';
import { AuthLayoutComponent } from './components/auth-layout/auth-layout.component';
import { ComparePasswordModule } from './components/register/_controls/basic-info/ComparePassword.Module';
import { HttpClient } from '@angular/common/http';
import { TranslateModule, TranslateLoader } from '@ngx-translate/core';
import { TranslateHttpLoader } from '@ngx-translate/http-loader';

// ghareebovich@gmail.com
// P@$$W0rd

@NgModule({
  declarations: [
    LoginComponent,
    RegisterBusinessUserComponent,
    RegisterNormalUserComponent,
    ForgetPasswordComponent,
    NewPasswordComponent,
    RegisterEntryPointComponent,
    // confirmEquelValidator,
    CompanyInfoComponent,
    StoreInfoComponent,
    ContactInfoComponent,
    BasicInfoComponent,
    ConfirmEmailComponent,
    AuthLayoutComponent
  ],
  imports: [
    CommonModule,
    AuthRoutingModule,
    FormsDependenciesModule,
    SharedModule,
    ReactiveFormsModule,
    MatFormFieldModule,
    MatInputModule,
    MatCheckboxModule,
    MatButtonModule,
    MatTabsModule,
    MatSelectModule,
    ComparePasswordModule,
    TranslateModule.forChild({
      loader: {
        provide: TranslateLoader,
        useFactory: httpTranslateLoader,
        deps: [HttpClient]
      }
    })
  ],
  exports: [
    MatFormFieldModule,
    MatInputModule,
    MatCheckboxModule,
    MatButtonModule,
    MatTabsModule,
    MatSelectModule,
  ],
  providers: [
    AuthApiService
  ]
})
export class AuthModule { }

// AOT compilation support
export function httpTranslateLoader(http: HttpClient) {
  return new TranslateHttpLoader(http);
}
