import { Attribute, Directive, Input } from '@angular/core';
import { Validator, NG_VALIDATORS, AbstractControl } from '@angular/forms';
import { Key } from 'protractor';

@Directive(
    {
        selector: '[appConfirmEqualValidators]',
        providers: [{ provide: NG_VALIDATORS, useExisting: CompareDirective, multi: true }]
    }

)

export class CompareDirective implements Validator {


    @Input() appConfirmEqualValidators: string;
    validate(control: AbstractControl): { [Key: string]: any } | null {
        const controlToCompare = control.parent.get(this.appConfirmEqualValidators);
        if (controlToCompare && controlToCompare.value !== control.value) {
            return { notEqual: true };
        }
        return null;
    }



}
