import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { ConfirmEmailComponent, LoginComponent, RegisterBusinessUserComponent, RegisterEntryPointComponent, RegisterNormalUserComponent } from './components';
import { AuthLayoutComponent } from './components/auth-layout/auth-layout.component';



const routes: Routes = [
  {
    path:'',
    component : AuthLayoutComponent,
    children : [
      { path:"register", children : [
          { path:"", component:RegisterEntryPointComponent },
          { path:"basic", component:RegisterNormalUserComponent },
          { path:"business", component:RegisterBusinessUserComponent },
        ]
      },
      { path:"login", component:LoginComponent },
      { path:"confirm-email", component:ConfirmEmailComponent },
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class AuthRoutingModule { }


