import { HttpErrorResponse } from '@angular/common/http';
import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl, Validators, AbstractControl } from '@angular/forms';
import { Router } from '@angular/router';
import { AlertService, AuthService } from 'src/app/core/services';
import {LoginRequest  } from '../../models/login-request.model';
import {AuthApiService} from '../../services/api/auth-api.service' ;


@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.scss'],
  providers:[AuthApiService]

})
export class LoginComponent implements OnInit {
errorMsg= '';
 constructor(
   private  _AuthApiService:AuthApiService,
   private  alertSrvc:AlertService,
   private  authSrvc:AuthService,
   private router : Router
   ) { }
 ngOnInit(): void { }

 get emailCtrl():AbstractControl { return this.myform.get('email') }
 get passwordCtrl():AbstractControl { return this.myform.get('password') }

  myform:FormGroup = new FormGroup({
    'email':new FormControl (null,[Validators.required,Validators.email]),
    'password':new FormControl (null,[Validators.required ,Validators.minLength(6)]),
    'rememberMe':new FormControl (null)
  })

  login(mydata){
    if(this.myform.invalid) {
      this.myform.markAllAsTouched()
      return this.alertSrvc.error('Complete all the fields')
    }
    let requestBody : LoginRequest = new LoginRequest ();
    console.log (requestBody)
    let myformValue = this.myform.value
    requestBody.email = myformValue.email
    requestBody.password = myformValue.password
    this._AuthApiService.login(requestBody).subscribe(
      (res)=>{
        this.alertSrvc.success('Logged In successfully')
        this.authSrvc.saveUserData(res.data)
        this.router.navigate(['/'])
      },(err:HttpErrorResponse)=>{
        this.alertSrvc.error(err.error.Message)
      }
    )
  }


  navigateToRegister(){
    this.router.navigate(['/register'])
  }

}
